<template>
  <div class="table-page" ref="tablePage">
    <Table
      :columns="tableHeader"
      :data="data"
      stripe
      :height="height"
      :loading="loading"
      class="table"
      @on-selection-change="$emit('on-selection-change', $event)"
      @on-sort-change="$emit('on-sort-change', $event)"
    >
      <template v-for="(item) in tableHeader" #[item.slot]="props">
        <slot :name="item.slot" v-bind="props"></slot>
      </template>
    </Table>
    <Page
      v-model="curPage"
      :total="total"
      show-elevator
      show-sizer
      show-total
      :page-size="pageSize"
      :page-size-opts="pageSizeOpts"
      @on-change="changePage"
      @on-page-size-change="changePageSize"
      class="page"
    />
  </div>
</template>

<script>
import { Table, Page } from "view-ui-plus";
import elementResizeDetector from "element-resize-detector";

export default {
  name: "viTablePage",
  components: { Table, Page },
  props: {
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    getData: {
      type: Function,
      default() {}
    },
    pageSizeOpts: {
      type: Array,
      default() {
        return [10, 20, 50, 100];
      }
    },
    addIndex: {
      type: Boolean,
      default: false,
    },
    addSelect: {
      type: Boolean,
      default: false,
    },
    isInitLoad: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    tableHeader() {
      return [
        ...(this.addSelect ? [{ type: "selection", width: 40 }] : []),
        ...(this.addIndex ? [{
          title: "序号",
          type: "index",
          width: 60,
          align: "center",
          indexMethod: (row, index) => (this.curPage - 1) * this.pageSize + index + 1
        }] : []),
        ...this.columns,
      ];
    },
  },
  data() {
    return {
      erd: null,
      height: 0,
      data: [],
      total: 100,
      curPage: 1,
      pageSize: 10,
      loading: false,
    };
  },
  methods: {
    changePageSize(pageSize) {
      this.curPage = 1;
      this.pageSize = pageSize;
      this.search();
    },
    changePage(page = 1) {
      this.curPage = page;
      if (this.loading) return; // 防止mounted和activated请求两次
      this.search();
    },
    async search() {
      try {
        this.loading = true;
        const params = {
          curPage: this.curPage,
          pageSize: this.pageSize,
        };
        const {
          data: { rows, count } = {},
          returnCode = 1,
        } = await this.getData(params) || {};
        if (returnCode === 0) {
          this.data = rows || [];
          this.total = count;
        }
      } catch (err) {
        this.$Message.error(err.message || err);
      } finally {
        this.loading = false;
      }
    },
  },
  mounted() {
    if (this.isInitLoad) {
      this.changePage();
    }
    this.erd = elementResizeDetector();
    this.erd.listenTo(this.$refs.tablePage, () => {
      this.height = this.$refs.tablePage.offsetHeight - 50;
    });
  },
  beforeUnmount() {
    this.erd.uninstall(this.$refs.tablePage);
  },
};
</script>

<style lang="less" scoped>
.table-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  & .table {
    flex: 1;
  }
  & .page {
    text-align: right;
    height: 50px;
    line-height: 50px;
    margin-right: 5px;
  }
}
</style>
